#game-section{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    width: 100%;
}

.context-container{
    width: 80%;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#text-context{
    width: 40%;
    text-align: center;
}

.avatar-container{
    width: 28%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.objetivo-container{
    width: 28%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0.8rem;
    border: #457b9d 2px solid;
    text-align: justify;
}

.objetivo-container h3{
    color: #457b9d;
    font-weight: bolder;
    margin-bottom: 2rem;
}

.objetivo-container p{
    text-align: justify;
    font-size: 1.1rem;
    line-height: 1.3rem;
}

.game-buttons-container{
    width: 100%;
    padding: 0 1rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}





